// ========================================================
// Dependencies
// This file is not exported as part of build, its only used for local dev
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/resets/normalize.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/resets/document.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/averta-fonts.scss';

// TODO: remove this when branding is solved.
:root {
    --eureka-layout-tile-border-radius: 8px;
    --chat-font: 'Averta';
    --eureka-core-color-secondary-90: #eaecff;
    --eureka-core-color-primary: #2945ff;
    --chat-bubble-radius: 16px;
    --chat-bubble-max-width: 266px;
    --chat-bubble-from-me-background-color: var(--eureka-core-color-primary);
    --chat-bubble-from-me-text-color: white;
    --chat-bubble-from-other-background-color: #f2f2f2;
    --chat-bubble-from-other-text-color: black;
}

body {
    font-family: var(--chat-font);
    background-color: #ccc;
}

.web-chat-root {
    max-width: 375px;
    margin: auto;
}
